@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&family=Poppins:wght@300;500;600;700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg : #1f1f38;
    --color-bg-variant : #2c2c6c;
    --color-primary : #4db5ff;
    --color-light : rgba(255,255,255,0.6);

    --transition:all 400ms ease ;

    --container-width-lg : 80%;
    --container-width-md : 86%;
    --container-width-sm : 90%;
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: white;
    line-height: 1.7;
    background-image: url(../src/assets/bg.jpg);
}


/* ============== GENERAL CSS =================*/

.container{
    max-width: var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}
section > h2, section > h5,h1{
    text-align: center;
    color:var(--color-primary);
} 

.text-light {
    color: var(--color-light);
}
a{
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover{
    color: white;
}

.btn{
    width :max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.btn :hover {
    background: white;
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
img{
    display: block;
    width: 100%;
    object-fit: conver;
}

/* ================= Media Quries (MEDIA DEVICES) =============*/

@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}

/* ================= Media Quries (SMALL DEVICES) =============*/

@media screen and (max-width:600px) {
    .container{
        width: var(--container-width-sm);
    }
    section >h2 {
        margin-top: 2rem;
    }
}